import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { DiscussionForumService } from 'src/app/core/services/discussion-forum.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./topic-dashboard.component.html",
  styleUrls: ["./topic-dashboard.component.css"],
})
export class TopicDashboardComponent implements OnInit {
  response: any;
  uploaddata: any;
  topiccount: any = 0 ;
  id: any;
  name: any;
  url: any;
  constructor(
    public datepipe: DatePipe,
    private createQuoteService: CreateQuoteService, 
    private commonService: CommonService,
    private discussionforumService: DiscussionForumService,
    private _Activatedroute:ActivatedRoute)
    {
    }

  ngOnInit()
  {
    debugger;
    this.id = this._Activatedroute.snapshot.paramMap.get("item.section.id");
    this.name = this._Activatedroute.snapshot.paramMap.get("item.section.name");
    this.url = window.location.href;
    localStorage.setItem('section', this.name); 
    localStorage.setItem('url', this.url); 
    this.getAllTopics(this.id);
    console.log(this.id);
  }

  topicDataList: any;
  getAllTopics(id)
  {
    this.discussionforumService
    .getAllTopicsBysectionID(id)
    .subscribe(
    (res) => 
    {
      this.topicDataList = res;
      console.log(this.topicDataList);
    },
    (error) => () => {
    }
    );
  }
}
